<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect @selects="selectTypes" @toggles="toggleContents" :ratings="ratings" :only-content="onlyContent" :select-type="selectType" :desc="desc"></ratingselect>
      <div class="rating-wrapper">
        <ul>
          <li v-for="rating in ratings" v-show="needShow(rating.rateType, rating.text)" class="rating-item">
            <div class="avatar">
              <img width="28" height="28" :src="rating.avatar">
            </div>
            <div class="content">
              <h1 class="names">{{rating.username}}</h1>
              <div class="star-wrapper">
                <star :size="24" :score="rating.score"></star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                <span class="icon-arrow_lift"></span>
                <span class="item" v-for="item in rating.recommend">{{item}}</span>
              </div>
              <div class="time">
                {{rating.rateTime | formateDate}}
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import star from '@/components/star/star'
import split from '@/components/split/split'
import ratingselect from '@/components/ratingselect/ratingselect'
import BScroll from 'better-scroll'
import {turn} from '@/common/js/date.js'
const ERR_OK = 0
const ALL = 2
export default {
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      ratings: [],
      onlyContent: true,
      selectType: ALL,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    }
  },
  created () {
    this.$http.get('/api/ratings').then((response) => {
      response = response.body
      if (response.errno === ERR_OK) {
        this.ratings = response.data
        this.$nextTick(() => {
          this.scroll = new BScroll(this.$refs.ratings, {
            click: true
          })
        })
      }
    })
  },
  methods: {
    selectTypes (type) {
      this.selectType = type
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    toggleContents () {
      this.onlyContent = !this.onlyContent
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    needShow (type, text) {
      if (this.onlyContent && !text) {
        return false
      }
      if (this.selectType === ALL) {
        return true
      } else {
        return this.selectType === type
      }
    }
  },
  components: {
    star,
    split,
    ratingselect
  },
  filters: {
    formateDate (time) {
      let date = new Date(time)
      return turn(date)
    }
  }
}
</script>
<style scoped>
  .ratings{
    position: absolute;
    top: 174px;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
  }
  .overview{
    display: flex;
    padding: 18px 0;
  }
  .overview-left{
    flex: 0 0 137px;
    padding: 6px 0;
    width: 137px;
    border-right: 1px solid rgba(7, 17, 27, 0.1);
    text-align: center;
  }
  @media only screen and (max-width: 320px){
    .overview-left{
      flex: 0 0 120px;
      width: 120px;
    }
 }
 .overview-left .score{
    margin-bottom: 6px;
    line-height: 28px;
    font-size: 24px;
    color: rgb(255, 153, 0);
 }
 .overview-left .title{
   margin-bottom: 8px;
   line-height: 12px;
   font-size: 12px;
   color: rgb(7, 17, 27);
 }
 .overview-left .rank{
   line-height: 10px;
   font-size: 10px;
   color: rgb(147, 153, 159);
 }
 .overview-right{
   flex: 1;
   padding: 6px 0 6px 24px;
 }
 @media only screen and (max-width: 320px){
   .overview-right{
     padding-left: 6px;
   }
 }
 .score-wrapper{
   margin-bottom: 8px;
   font-size: 0;
 }
 .score-wrapper .title{
   display: inline-block;
   line-height: 18px;
   vertical-align: top;
   font-size: 12px;
   color: rgb(7, 17, 27);
 }
 .score-wrapper .star{
   display: inline-block;
   margin: 0 12px;
   vertical-align: top;
 }
 .score-wrapper .score{
   display: inline-block;
   line-height: 18px;
   vertical-align: top;
   font-size: 12px;
   color: rgb(255, 153, 0);
 }
 .delivery-wrapper{
   font-size: 0;
 }
 .delivery-wrapper .title{
   line-height: 18px;
   font-size: 12px;
   color: rgb(7, 17, 27);
 }
 .delivery-wrapper .delivery{
   margin-left: 12px;
   font-size: 12px;
   color: rgb(147, 153, 159);
 }
 .rating-wrapper{
   padding: 0 18px;
 }
 .rating-item{
   display: flex;
   padding: 18px 0;
   border-bottom: 1px solid rgba(7, 17, 27, 0.1);
 }
 .rating-item .avatar{
   flex: 0 0 28px;
   width: 28px;
   margin-right: 12px;
 }
 .avatar img{
     border-radius: 50%;
 }
 .rating-item .content{
   position: relative;
   flex: 1;
 }
 .content .names{
   margin-bottom: 4px;
   line-height: 12px;
   font-size: 10px;
   color: rgb(7, 17, 27);
 }
 .content .star-wrapper{
   margin-bottom: 6px;
   font-size: 0;
 }
 .star-wrapper .star{
   display: inline-block;
   margin-right: 6px;
   vertical-align: top;
 }
 .star-wrapper .delivery{
   display: inline-block;
   vertical-align: top;
   line-height: 12px;
   font-size: 10px;
   color: rgb(147, 153, 159);
}
.content .text{
  margin-bottom: 8px;
  line-height: 18px;
  color: rgb(7, 17, 27);
  font-size: 12px;
}
.recommend{
  line-height: 16px;
  font-size: 0;
}
.recommend .icon-arrow_lift,
.recommend .item{
  display: inline-block;
  margin: 0 8px 4px 0;
  font-size: 9px;
}
.recommend .icon-arrow_lift{
  color: rgb(0, 160, 220);
}  
.recommend .item{
  padding: 0 6px;
  border: 1px solid rgba(7, 17, 27, 0.1);
  border-radius: 1px;
  color: rgb(147, 153, 159);
  background: #fff;
}
.content .time{
  position: absolute;
  top: 0;
  right: 0;
  line-height: 12px;
  font-size: 10px;
  color: rgb(147, 153, 159);
}
</style>



